import React, { useState, useEffect } from "react";

import {
  Button,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  Col,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label
} from "reactstrap";
import Cookies from "js-cookie";

import Axios from "axios";
import { Spinner } from "../../../component";

const AddItem = props => {
  const [loading, setLoading] = useState(false);
  const [formValue, setFormValue] = useState({
    name: "",
    satuan: "",
    satuan_id: "",
    subcategory: "",
    subCategory2: "",
    item_provinsi: [
      {
        provinsi_id: "",
        price: ""
      }
    ],
    image: ""
  });

  const [satuan, setSatuan] = useState([]);
  const [provinsi, setProvinsi] = useState([]);
  const [category, setCategory] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState(0);
  const [subCategory, setSubCategory] = useState([]);
  const [subCategory2, setSubCategory2] = useState([]);

  async function fetchSatuan() {
    const data = await Axios.get("https://antarblanja.com/api/satuan");
    setSatuan(data.data);
  }

  async function fetchCategories() {
    const data = await Axios.get("https://antarblanja.com/api/categories");
    setCategory(data.data);
  }

  async function fetchSubCategory(id) {
    const data = await Axios.get(
      `https://antarblanja.com/api/categories/sub/${id}`
    );
    setSubCategory(data.data);
  }

  async function fetchSubCategory2(id) {
    const data = await Axios.get(
      `https://antarblanja.com/api/categories/sub2/${id}`
    );
    setSubCategory2(data.data);
  }

  async function fetchProvinsi() {
    const data = await Axios.get(`https://antarblanja.com/api/daerah/provinsi`);
    setProvinsi(data.data);
  }

  useEffect(() => {
    fetchCategories();
    fetchSatuan();
    fetchProvinsi();
  }, []);

  const checking = () => {
    var found = 0;
    for (let [key, value] of Object.entries(formValue)) {
      if (!value) {
        found++;
        break;
      } else if (key === "image") {
      }
    }

    for (let i = 0; i < formValue.item_provinsi.length; i++) {
      for (let [key, value] of Object.entries(formValue.item_provinsi[i])) {
        if (!value) {
          found++;
          break;
        }
      }
    }
    if (found === 0) {
      console.log(formValue);
      const formData = new FormData();
      formData.append("name", formValue.name);
      formData.append("price", formValue.harga);
      formData.append("satuan", formValue.satuan);
      formData.append("satuan_id", formValue.satuan_id);
      formData.append("subcategory_id", formValue.subcategory);
      formData.append("subcategory_id2", formValue.subCategory2);
      formData.append("item_provinsi", JSON.stringify(formValue.item_provinsi));
      formData.append("image", formValue.image);
      uploadData(formData);
    } else {
      console.log(formValue);
      alert("Input Not Complete");
    }
  };

  const uploadData = form => {
    setLoading(true);
    Axios({
      method: "POST",
      url: "https://antarblanja.com/api/admin/item",
      headers: { Authorization: Cookies.get("admin_token") },
      data: form
    })
      .then(data => {
        alert("Submit Success");
        setFormValue({
          name: "",
          satuan: "",
          satuan_id: "",
          subcategory: "",
          subCategory2: "",
          item_provinsi: [
            {
              provinsi_id: "",
              price: ""
            }
          ],
          image: ""
        });
        document.getElementById("image").value = "";
        setLoading(false);
      })
      .catch(e => {
        alert("error");
        console.log(e);
        setLoading(false);
      });
  };

  return (
    <div className="animated fadeIn">
      <Spinner isLoading={loading} />
      <Col xs="12" md="12">
        <Card>
          <CardHeader>
            <strong>Basic Form</strong> Elements
          </CardHeader>
          <CardBody>
            <FormGroup row>
              <Col md="3">
                <Label htmlFor="text-input">Nama Barang</Label>
              </Col>
              <Col xs="12" md="9">
                <Input
                  type="text"
                  id="text-input"
                  value={formValue.name}
                  name="text-input"
                  placeholder="Nama Barang"
                  onChange={e => {
                    formValue.name = e.target.value;
                    setFormValue({ ...formValue });
                  }}
                />
                <FormText color="muted">This is a help text</FormText>
              </Col>
            </FormGroup>

            <FormGroup row>
              <Col md="3">
                <Label htmlFor="text-input">Kuantitas barang / satuan</Label>
              </Col>
              <Col md="9">
                <InputGroup>
                  <Input
                    type="text"
                    value={formValue.satuan}
                    id="input1-group1"
                    name="input1-group1"
                    placeholder="Kuantitas barang"
                    type="number"
                    onChange={e => {
                      formValue.satuan = e.target.value;
                      setFormValue({ ...formValue });
                    }}
                  />
                </InputGroup>
              </Col>
            </FormGroup>
            <FormGroup row>
              <Col md="3">
                <Label htmlFor="select">Pilih Satuan Barang</Label>
              </Col>
              <Col xs="12" md="9">
                <Input
                  type="select"
                  value={formValue.satuan_id}
                  name="select"
                  id="satuan"
                  onChange={e => {
                    formValue.satuan_id = e.target.value;
                    setFormValue({ ...formValue });
                  }}
                >
                  <option value={null}>Pilih satuan </option>
                  {satuan.map((item, index) => {
                    return (
                      <option value={item.satuan_id} key={index}>
                        {item.name}
                      </option>
                    );
                  })}
                </Input>
              </Col>
            </FormGroup>
            <FormGroup row>
              <Col md="3">
                <Label htmlFor="select">Pilih Category</Label>
              </Col>
              <Col xs="12" md="9">
                <Input
                  type="select"
                  value={selectedCategory}
                  name="select"
                  id="category"
                  onChange={e => {
                    const value = e.target.value;
                    const changeCategory = async () => {
                      await fetchSubCategory(value);
                      await fetchSubCategory2(value);
                    };
                    changeCategory();

                    setSelectedCategory(value);
                    formValue.subcategory = 0;
                    setFormValue({ ...formValue });
                  }}
                >
                  <option value={null}>Pilih Category</option>
                  {category.map((item, index) => {
                    return (
                      <option value={item.category_id} key={index}>
                        {item.name}
                      </option>
                    );
                  })}
                </Input>
              </Col>
            </FormGroup>

            <FormGroup row>
              <Col md="3">
                <Label htmlFor="select">Pilih SubCategory</Label>
              </Col>
              <Col xs="12" md="9">
                <Input
                  type="select"
                  value={formValue.subcategory}
                  name="select"
                  id="subcategory"
                  disabled={selectedCategory ? false : true}
                  onChange={e => {
                    formValue.subcategory = e.target.value;
                    setFormValue({ ...formValue });
                  }}
                >
                  <option value={null}>Pilih subCategory</option>
                  {subCategory.map((item, index) => {
                    return (
                      <option value={item.subcategory_id} key={index}>
                        {item.name}
                      </option>
                    );
                  })}
                </Input>
              </Col>
            </FormGroup>
            <FormGroup row>
              <Col md="3">
                <Label htmlFor="select">Pilih SubCategory2</Label>
              </Col>
              <Col xs="12" md="9">
                <Input
                  type="select"
                  value={formValue.subCategory2}
                  name="select"
                  id="subcategory2"
                  disabled={selectedCategory ? false : true}
                  onChange={e => {
                    formValue.subCategory2 = e.target.value;
                    setFormValue({ ...formValue });
                  }}
                >
                  <option value={null}>Pilih SubCategory2</option>
                  {subCategory2.map((item, index) => {
                    return (
                      <option value={item.subcategory2_id} key={index}>
                        {item.name}
                      </option>
                    );
                  })}
                </Input>
              </Col>
            </FormGroup>
            {formValue.item_provinsi.map((item, index) => {
              return (
                <FormGroup row key={index}>
                  <Col md="3" xs="3">
                    <Label htmlFor="select">Provinsi {index + 1}</Label>
                  </Col>
                  <Col xs="9" md="9">
                    <Card>
                      <CardBody>
                        <FormGroup row>
                          <Col xs="12" md="12">
                            <Input
                              type="select"
                              value={formValue.item_provinsi[index].provinsi_id}
                              name="select"
                              id="provinsi"
                              onChange={e => {
                                formValue.item_provinsi[index].provinsi_id =
                                  e.target.value;
                                setFormValue({ ...formValue });
                              }}
                            >
                              <option value={null}>Pilih Provinsi</option>
                              {provinsi.map((item, index) => {
                                return (
                                  <option value={item.provinsi_id} key={index}>
                                    {item.name}
                                  </option>
                                );
                              })}
                            </Input>
                          </Col>
                        </FormGroup>

                        <FormGroup row>
                          <Col md="12">
                            <InputGroup>
                              <InputGroupAddon addonType="prepend">
                                <InputGroupText>Rp.</InputGroupText>
                              </InputGroupAddon>
                              <Input
                                type="text"
                                id="input1-group1"
                                name="input1-group1"
                                placeholder="Harga"
                                type="number"
                                onChange={e => {
                                  formValue.item_provinsi[index].price =
                                    e.target.value;
                                  setFormValue({ ...formValue });
                                }}
                              />
                            </InputGroup>
                          </Col>
                        </FormGroup>
                      </CardBody>
                    </Card>
                    {index !== 0 && (
                      <Button
                        type="submit"
                        size="sm"
                        color="danger"
                        className="float-right"
                        style={{ padding: "5px 15px", marginLeft: "10px" }}
                        onClick={() => {
                          formValue.item_provinsi.splice(index, 1);
                          setFormValue({
                            ...formValue
                          });
                        }}
                      >
                        <svg
                          className="bi bi-trash"
                          width="1em"
                          height="1em"
                          viewBox="0 0 16 16"
                          fill="currentColor"
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z" />
                          <path
                            fillRule="evenodd"
                            d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4L4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"
                          />
                        </svg>
                      </Button>
                    )}
                    {formValue.item_provinsi.length - 1 == index && (
                      <Button
                        type="submit"
                        size="sm"
                        color="primary"
                        className="float-right"
                        style={{ padding: "5px 15px" }}
                        onClick={() => {
                          formValue.item_provinsi.push({
                            provinsi_id: "",
                            price: ""
                          });
                          setFormValue({ ...formValue });
                        }}
                      >
                        +
                      </Button>
                    )}
                  </Col>
                </FormGroup>
              );
            })}
            <FormGroup row>
              <Col md="3">
                <Label htmlFor="file-input">Gambar Item</Label>
              </Col>
              <Col xs="12" md="9">
                <input
                  type="file"
                  name="image"
                  id="image"
                  accept="image/*"
                  onChange={e => {
                    formValue.image = e.target.files[0];
                    setFormValue({ ...formValue });
                  }}
                />
              </Col>
            </FormGroup>
          </CardBody>
          <CardFooter>
            <Button
              type="submit"
              size="sm"
              color="primary"
              style={{ marginRight: 10 }}
              onClick={() => {
                checking();
              }}
            >
              <i className="fa fa-dot-circle-o"></i> Submit
            </Button>
          </CardFooter>
        </Card>
      </Col>
    </div>
  );
};

export default AddItem;
